body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body, #root, .wrapper{
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;

}



.column{
  margin: 1%;
  width: 25%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.textCenter{
  text-align: center;
}

.blockElmCenter{
  margin: auto;
}
.content{
  height: 100%;
  /* width: inherit; */
  overflow-y: auto;
}

.item{
  padding: 3%;
  background: white;
  margin:3%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  max-height: 10%;
  overflow-y: auto;
}

.leftBtn , .rightBtn{
  width: 10%;
}
.item-content{
  width:80%;
  max-width: 80%;
  display: -webkit-flex;
  display: flex;
  /* max-height: 10%; */
}


.headSection{
  height: 5%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background: pink
}
.main-content{
  height: 95%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;

}

.addSection{
  margin: auto;
}
